import React, { useState, useEffect } from 'react';
import { ViewState } from "@devexpress/dx-react-scheduler";
import dayjs from 'dayjs';

import Paper from '@material-ui/core/Paper';

import {
  Scheduler,
  WeekView,
  Appointments,
  Toolbar,
  TodayButton,
  DateNavigator,
  AppointmentTooltip,
} from '@devexpress/dx-react-scheduler-material-ui';

import { useStore } from '../../store/DataProvider';

function Calendar() {

  const { state: { prices, workers, appointments }, actions: { getAppointments }} = useStore();

  const [selectedDate, setSelectedDate] = useState(dayjs().format());
  useEffect(() => {
    getAppointments();
  }, [getAppointments]);

  const data = appointments.map(item => {
    const worker = workers.find(worker => worker._id === item.worker);
    const serviceList = prices.filter(price => item.serviceIds.includes(price._id)).map(price => price.title).join(', ');
    return {
      startDate: dayjs(item.time).format(),
      endDate: dayjs(item.timeEnd).format(),
      title: `${item.name} ${item.lastName},
       Total: ${item.total},
       Worker: ${worker ? worker.name : 'Not matter'}
       Services: ${serviceList}
       `,
      id: item._id,
      additional: {...item}
    }
  });

  return (
    <Paper>
      <Scheduler
        data={data}
      >
        <ViewState
          currentDate={selectedDate}
          onCurrentDateChange={setSelectedDate}
        />
        <WeekView startDayHour={8} endDayHour={19} />
        <Appointments />
        <Toolbar />
        <DateNavigator />
        <TodayButton />
        <AppointmentTooltip/>
      </Scheduler>
    </Paper>
  );
}

export default Calendar;
