import React, { useState, useCallback } from 'react';
import { MuiPickersUtilsProvider, DatePicker, DateTimePicker } from '@material-ui/pickers';
import PerfectScrollbar from 'react-perfect-scrollbar';
import MaterialTable from 'material-table';
import DayjsUtils from '@date-io/dayjs';
import dayjs from 'dayjs';

import FormControl from '@material-ui/core/FormControl';
import InputLabel from '@material-ui/core/InputLabel';
import TextField from '@material-ui/core/TextField';
import MenuItem from '@material-ui/core/MenuItem';
import { makeStyles } from '@material-ui/styles';
import Select from '@material-ui/core/Select';
import Chip from '@material-ui/core/Chip';

import NavigateBeforeIcon from '@material-ui/icons/NavigateBefore';
import NavigateNextIcon from '@material-ui/icons/NavigateNext';
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';
import ExportIcon from '@material-ui/icons/CloudDownload';
import FirstPageIcon from '@material-ui/icons/FirstPage';
import LastPageIcon from '@material-ui/icons/LastPage';
import FilterIcon from '@material-ui/icons/FilterList';
import CancelIcon from '@material-ui/icons/Cancel';
import SearchIcon from '@material-ui/icons/Search';
import CheckIcon from '@material-ui/icons/Check';
import BlockIcon from '@material-ui/icons/Block';
import EditIcon from '@material-ui/icons/Edit';
import DoneIcon from '@material-ui/icons/Done';
import AddIcon from '@material-ui/icons/Add';

import { useStore } from '../../store/DataProvider';
import Input from '@material-ui/core/Input';
import Checkbox from '@material-ui/core/Checkbox';
import ListItemText from '@material-ui/core/ListItemText';

const useStyles = makeStyles(theme => ({
  root: {
    padding: theme.spacing(2)
  },
  spacer: {
    display: 'flex',
    flex: 1,
    flexGrow: 1,
  },
  grow: {
    flexGrow: 1,
  },
  fab: {
    position: 'absolute',
    bottom: theme.spacing(2),
    right: theme.spacing(2),
  },
  content: {
    padding: 0,
  },
  nameContainer: {
    display: 'flex',
    alignItems: 'center'
  },
  avatar: {
    marginRight: theme.spacing(2)
  },
  actions: {
    justifyContent: 'flex-end'
  },
  formControl: {
    margin: theme.spacing(1),
    minWidth: 120,
    width: '100%',
  }
}));

const ITEM_HEIGHT = 48;
const ITEM_PADDING_TOP = 8;
const MenuProps = {
  PaperProps: {
    style: {
      maxHeight: ITEM_HEIGHT * 4.5 + ITEM_PADDING_TOP,
      width: 250,
    },
  },
};

function Appointments() {
  const classes = useStyles();
  const {
    state: {
      appointments,
      prices,
      workers
    },
    actions: {
      updateAppointment,
      addAppointment,
    }
  } = useStore();

  const [loading, setLoading] = useState(false);

  const handleAdd = useCallback(data => {
    setLoading(true);
    return addAppointment(data).then(() => setLoading(false));
  }, [addAppointment]);

  const handleUpdate = useCallback(data => {
    setLoading(true);
    return updateAppointment(data).then(() => setLoading(false));
  }, [updateAppointment]);

  const workerLookup = { 0: 'Not matter' };

  workers.forEach(worker => workerLookup[worker._id] = worker.name);

  const columns = [
    {
      cellStyle: { minWidth: 150 },
      title: 'Full Name',
      field: 'name',
      render: rowData => `${rowData.name} ${rowData.lastName}`,
    },
    {
      cellStyle: { minWidth: 150 },
      title: 'Phone',
      field: 'phone',
      type: 'numeric',
    },
    {
      cellStyle: { minWidth: 150 },
      title: 'Email',
      field: 'email',
    },
    {
      cellStyle: { minWidth: 150 },
      title: 'Note',
      field: 'note',
      filtering: false,
      editComponent: props => (
        <TextField
          label="Note"
          multiline
          rowsMax="10"
          value={props.value}
          onChange={e => props.onChange(e.target.value)}
        />
      )
    },
    {
      cellStyle: { minWidth: 250 },
      title: 'Address',
      field: 'address',
      filtering: false,
      render: rowData => (
        <span>
          <p><b>City:</b> {rowData.city}</p>
          <p><b>Post:</b> {rowData.post}</p>
          <p><b>Address:</b> {rowData.address}</p>
          <p><b>Home number:</b> {rowData.homeNumber}</p>
          <p><b>Additional address:</b> {rowData.additionalAddress}</p>
        </span>
      ),
      editComponent: ({ rowData, onRowDataChange }) => (
        <>
          <TextField
            label="City"
            value={rowData.city}
            onChange={e => onRowDataChange({ ...rowData, city: e.target.value })}
          />
          <TextField
            label="Post"
            type='number'
            value={rowData.post}
            onChange={e => onRowDataChange({ ...rowData, post: e.target.value })}
          />
          <TextField
            label="Address"
            value={rowData.address}
            onChange={e => onRowDataChange({ ...rowData, address: e.target.value })}
          />
          <TextField
            label="Home Number"
            value={rowData.homeNumber}
            onChange={e => onRowDataChange({ ...rowData, homeNumber: e.target.value })}
          />
          <TextField
            label="Additional Address"
            value={rowData.additionalAddress}
            onChange={e => onRowDataChange({ ...rowData, additionalAddress: e.target.value })}
          />
        </>
      )
    },
    {
      cellStyle: { minWidth: 150 },
      title: 'Birthday',
      field: 'birthday',
      filtering: false,
      render: rowData => dayjs(rowData.birthday).format('MMMM DD, YYYY'),
      editComponent: props => (
        <MuiPickersUtilsProvider utils={DayjsUtils}>
          <DatePicker
            label='Birthday'
            value={dayjs(props.value)}
            format='DD/MM/YYYY'
            onChange={date => props.onChange(date.toDate())}
          />
        </MuiPickersUtilsProvider>
      ),
    },
    {
      cellStyle: { minWidth: 100 },
      title: 'Total',
      field: 'total',
      type: 'numeric',
      render: rowData => `${rowData.total} €`
    },
    {
      cellStyle: { minWidth: 100 },
      title: 'Status',
      field: 'status',
      lookup: { 0: 'Pending', 1: 'Accepted', 2: 'Declined' },
      render: rowData => (
        <Chip
          label={rowData.status === 1 ? 'Accepted' : rowData.status === 0 ? 'Pending' : 'Declined'}
          color={rowData.status === 1 ? 'primary' : rowData.status === 0 ? 'secondary' : 'default'}
        />
      ),
      editComponent: props => (
        <FormControl className={classes.formControl}>
          <InputLabel id="select-label">Status</InputLabel>
          <Select
            labelId="select-label"
            value={typeof props.value === 'undefined' ? 0 : props.value}
            onChange={e => props.onChange(e.target.value)}
          >
            <MenuItem value=''>Select Status</MenuItem>
            <MenuItem value={0}>Pending</MenuItem>
            <MenuItem value={1}>Accepted</MenuItem>
            <MenuItem value={2}>Declined</MenuItem>
          </Select>
        </FormControl>
      ),
    },
    {
      cellStyle: { minWidth: 250 },
      title: 'Schedule time',
      field: 'time',
      render: rowData => `${dayjs(rowData.time).format('MMMM DD, YYYY HH:mm')} - ${dayjs(rowData.timeEnd).format('MMMM DD, YYYY HH:mm')}`,
      editComponent: ({ value, onChange}) => (
        <MuiPickersUtilsProvider utils={DayjsUtils}>
          <DateTimePicker
            label='Schedule time'
            value={dayjs(value)}
            format='DD/MM/YYYY HH:mm'
            onChange={date => onChange(date.toDate())}
          />
        </MuiPickersUtilsProvider>
      ),
    },
    {
      cellStyle: { minWidth: 150 },
      title: 'Service list',
      field: 'serviceIds',
      editComponent: ({ onChange, value = [] }) => (
        <FormControl className={classes.formControl}>
          <InputLabel id="checkbox-label">Services</InputLabel>
          <Select
            labelId="checkbox-label"
            multiple
            value={value}
            onChange={e => onChange(e.target.value)}
            input={<Input />}
            renderValue={selected => prices.filter(price => selected.includes(price._id)).map(price => price.title).join(', ')}
            MenuProps={MenuProps}
          >
            {prices.map(price => (
              <MenuItem key={price._id} value={price._id}>
                <Checkbox checked={value.includes(price._id)} />
                <ListItemText primary={price.title} />
              </MenuItem>
            ))}
          </Select>
        </FormControl>
      ),
      render: rowData => rowData.serviceIds.map(item => {
        const price = prices.find(p => p._id === item);
        return price && <Chip key={price._id} label={price.title}/>;
      })
    },
    {
      cellStyle: { minWidth: 150 },
      title: 'Worker',
      field: 'worker',
      lookup: workerLookup,
      render: rowData => {
        const worker = workers.find(item => rowData.worker === item._id);
        return worker ? worker.name : 'Not matter';
      },
      customFilterAndSearch: (term, rowData) => {
        const newTerm = term.map(item => item === '0' ? null : item );
        return newTerm.includes(rowData.worker);
      },
      editComponent: props => (
        <FormControl className={classes.formControl}>
          <InputLabel id="select-worker-label">Worker</InputLabel>
          <Select
            labelId="select-worker-label"
            value={props.value}
            onChange={e => props.onChange(e.target.value)}
          >
            <MenuItem value={0}>Not matter</MenuItem>
            {workers.map(worker => <MenuItem value={worker._id}>{worker.name}</MenuItem>)}
          </Select>
        </FormControl>
      ),
    },
    {
      cellStyle: { minWidth: 150 },
      type: 'date',
      title: 'Created At',
      field: 'date',
      editable: 'never',
      filtering: false,
      render: rowData => rowData && dayjs(rowData.date).format('MMMM DD, YYYY'),
    },
  ];

  return (
    <div className={classes.root}>
      <PerfectScrollbar>
        <MaterialTable
          isLoading={loading}
          title="Appointments list"
          columns={columns}
          data={appointments.map(item => ({...item}))}
          icons={{
            Check: CheckIcon,
            Export: ExportIcon,
            Add: AddIcon,
            Filter: FilterIcon,
            Edit: EditIcon,
            Delete: CancelIcon,
            Search: SearchIcon,
            Clear: CancelIcon,
            FirstPage: FirstPageIcon,
            NextPage: NavigateNextIcon,
            LastPage: LastPageIcon,
            PreviousPage: NavigateBeforeIcon,
            ResetSearch: CancelIcon,
            SortArrow: ExpandMoreIcon,
          }}
          options={{
            exportButton: true,
            sorting: true,
            addRowPosition: 'first',
            filtering: true,
          }}
          editable={{
            onRowAdd: handleAdd,
            onRowUpdate: (newData, oldData) => handleUpdate(newData),
          }}
        />
      </PerfectScrollbar>
    </div>
  );
}

export default Appointments;
